import React, { memo, useMemo } from 'react';
import { COLORS, IRestProps, mergeRestProps } from '../../common';
import { PointTip } from './styled';

type TipProps = {
  // 以下为内部属性，PropsFromSlider
  point?: number,
  isMove?: boolean,
  formatter?: Function,

  // 以下为用户自定义属性
  bgColor?: string,
  textColor?: string,
} & IRestProps;

export const SliderTip = memo((props: TipProps) => {
  const { point = 0,  isMove, formatter,
    bgColor = 'rgba(22, 22, 22, 0.88)',
    textColor = COLORS.WHITE,
    style, ...rest } = props;
  const tipStyle = useMemo(() => ({
    backgroundColor: bgColor,
    color: textColor
  }), [bgColor, textColor]);

  return (
    <PointTip
      {...mergeRestProps({
      ...rest,
      style:tipStyle
    }, '')}
      role={isMove ? 'move' : 'hide'}>
      {formatter ? formatter(point) : point}
    </PointTip>
  );
});

SliderTip.displayName = 'sliderTip';